.listOrders {
    width: 100%;
    height: 100%;
}

.order {
    background-color: #1C1C21;
    border-radius: 30px;
}

.orderTitle {
    display: flex;
}

.orderDetail {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.orderNumber {
    flex-grow: 1;
}

.price {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.miniatures {
    flex-grow: 1;
    display: flex;
    flex-direction: row;
    justify-content: left;
}

.orderFeed {
    overflow-y: auto;
    height: calc(100% - 56px);
}